import axios from 'axios';

const baseUrl = '/api/todos'

const getAll = async () => {
  const response = await axios.get(baseUrl);

  return response.data;
};

const addTodo = async (title) => {
  const todoToAdd = {
    title: title,
    important: false,
  };

  const response = await axios.post(baseUrl, todoToAdd);

  return response.data;
};

const toggleTodoImportance = async (id) => {
  const todoToUpdate = await axios.get(`${baseUrl}/${id}`);
  console.log('Todo to update (axios service): ');
  console.log(todoToUpdate.data);

  const newUpdatedTodo = {
    title: todoToUpdate.data.title,
    important: !todoToUpdate.data.important,
  };

  try {
    const response = await axios.put(`${baseUrl}/${id}`, newUpdatedTodo);

    return response.data;
  }
  catch (error) {
    console.log(`Deu pau ${error}`);
  }
  
};

const deleteTodo = async (id) => {
  try {
    await axios.delete(`${baseUrl}/${id}`);
    return true;
  }
  catch (error) {
    return error;
  }
};

export default {
  getAll,
  addTodo,
  toggleTodoImportance,
  deleteTodo,
};