import React from 'react';
import './ArticleItem.css';

const ArticleItem = ({ article, deleteArticle, toggleArticleStatus }) => {
  return (
    <div className="article-item box">
      <div className="article-title">
        {article.title} {article.read ? '(read)' : ''}
      </div>
      <div className="article-buttons-list">
        <span className="article-button" onClick={() => toggleArticleStatus(article.id)}>&#10004;</span>
        <span className="article-button" onClick={() => deleteArticle(article.id)}>&#10005;</span>
      </div>
    </div>
  );
};

export default ArticleItem;