import axios from 'axios';

const baseUrl = '/api/articles'

const getAll = async () => {
  const response = await axios.get(baseUrl);

  return response.data;
};

const addArticle = async (title, url) => {
  const articleToAdd = {
    title: title,
    url: url,
  };

  const response = await axios.post(baseUrl, articleToAdd);

  return response.data;
};

const toggleArticleStatus = async (id) => {
  const articleToUpdate = await axios.get(`${baseUrl}/${id}`);
  console.log('Article to update (axios service): ');
  console.log(articleToUpdate.data);

  const newUpdatedArticle = {
    title: articleToUpdate.data.title,
    url: articleToUpdate.data.url,
    read: !articleToUpdate.data.read
  };

  try {
    const response = await axios.put(`${baseUrl}/${id}`, newUpdatedArticle);

    return response.data;
  }
  catch (error) {
    console.log(`Deu pau ${error}`);
  }
  
};

const deleteArticle = async (id) => {
  try {
    await axios.delete(`${baseUrl}/${id}`);
    return true;
  }
  catch (error) {
    return error;
  }
};

export default {
  getAll,
  addArticle,
  toggleArticleStatus,
  deleteArticle,
};