import React from 'react';
import './TodoItem.css';

const TodoItem = ({ todo, toggleTodoImportance, deleteTodo }) => {
  return (
    <div className="todo-item box">
      <div className="todo-title">
        {todo.title} {todo.important ? '(important)' : ''}
      </div>
      <div className="todo-buttons-list">
        <span className="todo-button" onClick={() => toggleTodoImportance(todo.id)}>&#33;</span>
        <span className="todo-button" onClick={() => deleteTodo(todo.id)}>&#10005;</span>
      </div>
    </div>
  );
};

export default TodoItem;