import React, { useState, useEffect } from 'react';
import AddTodoForm from './components/AddTodoForm';
import TodoItem from './components/TodoItem';
import TodosService from './services/todos';
import Notification from '../shared_components/Notification';

const Todo = () => {
  const [todos, setTodos] = useState([]);
  const [todo, setTodo] = useState('');
  const [notificationMessage, setNotificationMessage] = useState(null);

  // Notification helper (to be extracted to utils)
  const setNotification = (message) => {
    setNotificationMessage(message);
    setTimeout(() => {
      setNotificationMessage(null);
    }, 2000);
  };

  // Load initial todos
  useEffect(() => {
    TodosService
      .getAll()
      .then((initialTodos) => setTodos(initialTodos.sort((a, b) => b.important - a.important)));
  }, []);

  // Keep todo-add form in sync with state
  const handleChange = (event) => {
    setTodo(event.target.value);
  };

  // Handle adding new todo
  const handleAddTodo = async (event) => {
    event.preventDefault();

    if (todo === "" || todo.trim() === "") {
      setNotification('The "Add todo" field can\'t be empty');
    } else {
      const addedTodo = await TodosService.addTodo(todo);

      setTodos([...todos, addedTodo]);
      setNotification('Todo added successfully!');
    }

    setTodo('');
  };

  // Handle deleting todo
  const handleDeleteTodo = async (id) => {
    TodosService
      .deleteTodo(id)
      .then(() => {
        setTodos(todos.filter((todo) => todo.id !== id));
        setNotification('Todo deleted successfully!');
      })
      .catch((error) => {
        setNotification(error.message);
      });
  };

  // Handle change of importance of todo
  const handleToggleTodoImportance = async (id) => {
    TodosService
      .toggleTodoImportance(id)
      .then((updatedTodo) => {
        const updatedTodos = todos.map((todo) => todo.id === id ? updatedTodo : todo);
        setTodos(updatedTodos.sort((a, b) => b.important - a.important));
        setNotification(`"${updatedTodo.title}" set to ${updatedTodo.important ? '"important".' : '"not important".'}`);
      })
      .catch((error) => console.log('Unable to update todo', error));
  };

  // Create displayable list of todos
  const todosToDisplay = todos.map((todo) => 
    <TodoItem 
      key={todo.id}
      todo={todo}
      toggleTodoImportance={handleToggleTodoImportance}
      deleteTodo={handleDeleteTodo} 
    />)

  return (
    <div className="box">
      <div style={disclaimerStyle} className="box-no-shadow">This is a demonstration app. To-dos are deleted from the server automatically after 5 minutes.</div>
      <Notification message={notificationMessage} />
      <AddTodoForm 
        value={todo}
        onSubmit={handleAddTodo}
        onChange={handleChange}
      />
      {todosToDisplay}
    </div>
  );
};

const disclaimerStyle = {
  color: 'var(--grey)',
  marginTop: 0,
};

export default Todo;