import React, { useState, useEffect } from 'react';
import AddArticleForm from './components/AddArticleForm';
import ArticlesService from './services/articles';
import ArticleItem from './components/ArticleItem';
import Notification from '../shared_components/Notification';

const ArticlesManager = () => {
  const [articles, setArticles] = useState([]);
  const [title, setTitle] = useState('');
  const [url, setUrl] = useState('');
  const [notificationMessage, setNotificationMessage] = useState(null);

  // Notification helper (to be extracted to utils)
  const setNotification = (message) => {
    setNotificationMessage(message);
    setTimeout(() => {
      setNotificationMessage(null);
    }, 2000);
  };

  // Fetch initial articles and order them so that "read" ones go to the bottom of the list
  useEffect(() => {
    ArticlesService
      .getAll()
      .then((initialArticles) => {
        setArticles(initialArticles.sort((a, b) => a.read - b.read));
      })
      .catch((error) => setNotification(`Unable to fetch initial articles: ${error.message}`));
  }, []);

  // Synchronize text inputs and state
  const handleChange = (event) => {
    switch (event.target.id) {
      case 'article-title':
        setTitle(event.target.value);
        break;
      case 'article-url':
        setUrl(event.target.value);
        break;
      default:
        break;
    }
  };

  // Handle adding a new article
  const handleAddArticle = (event) => {
    event.preventDefault();

    if (!title || !url) {
      setNotification('There can be no empty fields!');
      return;
    } else {
      ArticlesService
      .addArticle(title, url)
      .then((addedArticle) => {
        setArticles([...articles, addedArticle]);
        setNotification('Article added successfully!');
      })
      .catch((error) => setNotification(`Unable to add article: ${error.message}`));
    }

    setTitle('');
    setUrl('');
  };

  // Handle changing the read status of an article
  const handleToggleArticleStatus = (id) => {
    ArticlesService
      .toggleArticleStatus(id)
      .then((updatedArticle) => {
        const updatedArticles = articles.map((article) =>
          (article.id === id ? updatedArticle : article));
        setArticles(updatedArticles.sort((a, b) => a.read - b.read));
        setNotification(`"${updatedArticle.title}" marked as ${updatedArticle.read ? 'read.' : 'not read.'}`);
      })
      .catch((error) => {
        setNotification(`Unable to change status of the article: ${error.message}`);
      });
  };

  // Handle deleting article
  const handleDeleteArticle = async (id) => {
    ArticlesService
      .deleteArticle(id)
      .then(() => {
        setArticles(articles.filter((article) => article.id !== id));
        setNotification('Article deleted successfully!');
      })
      .catch((error) => {
        setNotification(`Could not delete article: ${error.message}`);
      });
  };

  // Create displayable list of articles
  const articlesToDisplay = articles.map((article) => {
    return (
      <ArticleItem
        key={article.id}
        article={article}
        toggleArticleStatus={handleToggleArticleStatus}
        deleteArticle={handleDeleteArticle}
      />
    );
  });

  return (
    <div className="box">
      <div style={disclaimerStyle} className="box-no-shadow">This is a demonstration app. The articles are deleted from the server automatically after 5 minutes.</div>
      <Notification message={notificationMessage} />
      <AddArticleForm
        title={title}
        url={url}
        onSubmit={handleAddArticle}
        onChange={handleChange}
      />
      {articlesToDisplay}
    </div>
  );
};

const disclaimerStyle = {
  color: 'var(--grey)',
  marginTop: 0,
};

export default ArticlesManager;
