import axios from 'axios';

const baseUrl = '/api/visits';

const getVisits = async () => {
  const response = await axios.get(baseUrl);

  return response.data;
};

const incrementVisits = async () => {
  const currentVisits = await axios.get(baseUrl);
  const visitObjectId = currentVisits.data[0].id;

  const incrementedVisits = {
    visits: currentVisits.data[0].visits + 1,
  };

  const response = await axios.put(`${baseUrl}/${visitObjectId}`, incrementedVisits);

  return response.data.visits;
}

export default { 
  getVisits,
  incrementVisits,
};