import React, { useState, useEffect } from 'react';
import VisitsService from './services/visits';

const VisitCounter = () => {
  const [visits, setVisits] = useState('Loading...');

  useEffect(() => {
    VisitsService
      .incrementVisits()
      .then((visits) => setVisits(visits));
  }, []);

  return (
    <div className="box">
      <p>{`Visits: ${visits}`}</p>
    </div>
  );
};

export default VisitCounter;